.zip {
  display: flex;
  position: relative;
  border-radius: 4px;
  justify-content: space-between;
  width: 100%;
  background: linear-gradient(to right, black 55%, rgba(0, 0, 0, 0.5));
  margin: 0.5em;
  margin-top: 0em;
  padding: 0.5em;
  min-height: 150px;

  .img {
    max-height: 150px;
    width: 100%;

    img {
      height: 100%;
      //   width: 100%;
      max-width: 100%;
      border-radius: 4px;
      //   text-indent: -10000px;
    }
  }

  .name {
    font-weight: bold;
    margin-bottom: 0.5em;
  }

  .desc {
    margin-bottom: 0.5em;
  }

  sup,
  sub {
    vertical-align: baseline;
    position: relative;
    top: -0.4em;
  }

  .stats {
    text-align: right;
    display: flex;
    flex-direction: column;
    align-items: bottom;
    position: absolute;
    bottom: 1em;
    right: 0px;
    width: 230px;

    .value {
      margin-left: auto;
      margin-right: 0px;
    }

    .components {
      display: flex;
      justify-content: space-between;
      margin-right: 1em;

      .component {
        width: 25px;
        text-align: center;
      }
    }
  }

  .total,
  .name,
  .stat,
  .desc {
    margin-right: 1em;
  }

  .name,
  .title,
  .desc {
    text-shadow: 0px 0px 3px black, 0px 0px 2px black;
  }

  //   .name {
  //     position: absollute;
  //     top: 0px;
  //   }

  .controls {
    width: 200px;
    margin-right: 0px;
    margin-top: 1em;
    margin-left: auto;
  }

  &:hover {
    background: linear-gradient(to right, black 55%, rgba(40, 30, 50, 0.5));
    cursor: pointer;
  }
}

// .zip:hover .stats {
